.cartSliderProduct_container {
    height: auto;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-align: center;
    position: relative;
    margin-top: 1rem;
    padding: 1.5rem;
    
    .cartSliderProduct_content {
        width: 100%;

        .cartSliderProduct_image {
            height: 15rem;
            width: 15rem;
            display: flex;
            justify-content: center;
            align-items: center;
            margin: 0 auto;
            
            img {
                width: 100%;
                height: 100%;
            }
        }
        .product_price{
            display: flex;
            width:100%;
            justify-content: space-evenly;
            align-items: center;
        }
    }

    .product_btn_remove {

        &:hover {
            cursor: pointer;
        }
    }
}